<template>
  <div class="admin">
    <el-container>
      <el-aside :width="`${this.$store.state.width}px`" style="background:#545c64;overflow: hidden;transition: 0.3s">
        <LeftNav></LeftNav>
      </el-aside>
      <el-container>
        <el-header style="text-align: right; color:#fff;">
          欢迎: admin<el-link style="padding: 0 30px;" @click="logout" :underline="false" type="primary">登出</el-link>
        </el-header>
        <el-main>
          <router-view/>
        </el-main>
        <el-footer>
          footer
        </el-footer>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import LeftNav from './components/LeftNav'
import axios from 'axios'
export default {
  name: 'home',
  components: {LeftNav},
  data: function () {
    return {
    }
  },
  created () {
    window.onresize = () => {
      if (window.innerWidth < 1000) {
        this.$store.dispatch('turnOff')
      } else {
        this.$store.dispatch('turnOn')
      }
    }
  },
  methods: {
    logout: function () {
      axios.get('/api/logout')
        .then(res => {
          console.log(res.data)
          if (res.data.code === 0) {
            this.$router.push('/login')
            this.$message.success('登出成功')
          }
        })
    }
  }
}
</script>

<style scoped>
.el-aside{
  background: #545c64;
}
.admin,.el-container{
  height: 100vh;
}
.el-header{

}
.el-header, .el-footer {
  background-color: rgba(255,255,255,.3);
  color: #333;
  text-align: center;
  line-height: 60px;
}

.el-aside {
  background-color: #D3DCE6;
  color: #333;
  line-height: 200px;
}

.el-main {
  background-color: rgba(255,255,255,.1);
  color: #fff;
}

body > .el-container {
  margin-bottom: 40px;
}
</style>



