<template>
  <div class="left-nav">
    <el-menu
      default-active="2-4-2"
      text-color="#fff"
      active-text-color="#ffd04b"
      active-background-color="#ffd04b"
      class="el-menu-vertical-demo"
      @open="handleOpen"
      @close="handleClose"
      :collapse="this.$store.state.collapse"
      background-color="#545c64"
    >
      <el-menu-item
        index="1-1-1"
        keyword="home"
        style="text-align: center;"
      >
        <router-link to="/admin/home">主页</router-link>
      </el-menu-item>
      <el-submenu v-for="(item1, i) in navList" :index="i+'1'" v-bind:key="i">
        <template slot="title">
          <i class="el-icon-menu"></i>
          <span slot="title">{{item1.title}}</span>
        </template>
        <el-menu-item-group v-for="(item2,j) in item1.subList" :title="item2.title"  v-bind:key="j">
          <el-menu-item
            v-for="(item3, k) in item2.subItem"
            v-bind:key="k"
            :index="(i+1)+'-'+(j+1)+'-'+(k+2)"
            @click="handleSelect"
            :keyword="item3"
            :keysubtype="item2.title"
            :keytype="item1.title"
          >
<!--            添加点击的路由跳转-->
            <router-link :to="'/admin/exam/'+item3">{{item3}}</router-link>
          </el-menu-item>
        </el-menu-item-group>

      </el-submenu>

    </el-menu>
  </div>
</template>

<script>
import navList from '../../config/navList'
export default {
  name: 'LeftNav',
  props: ['collapse'],
  data () {
    return {
      navList: navList
    }
  },
  methods: {
    handleOpen (key, keyPath) {
      // console.log(key, keyPath)
    },
    handleClose (key, keyPath) {
      // console.log(key, keyPath)
    },
    handleSelect (e) {
      console.log(e.$attrs.keytype)
      this.$store.dispatch('select', {
        select: e.$attrs.keyword,
        type: e.$attrs.keytype,
        subtype: e.$attrs.keysubtype,
        current: e.$attrs.current
      })
    }
  }
}
</script>

<style scoped>
a{
   color: inherit;text-decoration: none;
 }
.left-nav{
  line-height: normal;
  background: #545c64;
}
.el-menu{
  border: none;
}
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 200px;
  /*min-height: 400px;*/
}
.el-menu-item .is-active{
  background: #aaa;
}
.el-submenu__title{
  text-align: left;
}
</style>



